h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page {
  letter-spacing: .0626em;
  scroll-behavior: smooth;
  font-family: Merriweather, serif;
  line-height: 1.5;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  min-width: 320px;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  background-color: #fff;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

a {
  color: #212529;
}

.container {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  padding-right: 20px;
}

.btn {
  text-transform: uppercase;
  letter-spacing: .15rem;
  color: #fff;
  border: 0;
  border-radius: .25rem;
  padding: 1.25rem 2rem;
  font-size: 80%;
  display: inline-block;
  box-shadow: 0 .1875rem .1875rem rgba(0, 0, 0, .1) !important;
}

.menu {
  height: 30vh;
  z-index: 1;
  overflow: auto;
